/* eslint-disable @typescript-eslint/no-explicit-any */
/**
 * Orval Mutator (TypeScript)
 * - Centralized request handler for all generated API calls
 * - Responsibilities:
 *   • Prefix base URL from window.API_BASE_URL
 *   • Prefer window.APIConfig.makeRequest if available (test shim)
 *   • Attach Authorization header from window.__AUTH_TOKEN__ or localStorage
 *   • Add Idempotency-Key for POST/PUT/PATCH when missing
 *   • Queue operations to window.IndexedDBOutbox when offline
 *   • Use OptimisticAPIClient.putWithOptimisticLock when present for PUTs
 *   • Normalize JSON/plain-text responses and throw on HTTP errors
 */

declare global {
  interface Window {
    API_BASE_URL?: string;
    APIConfig?: any;
    OptimisticAPIClient?: any;
    IndexedDBOutbox?: any;
    __AUTH_TOKEN__?: string;
    customInstance?: any;
  }
}

function toPlainHeaders(headersInit: HeadersInit | undefined): Record<string, string> {
  const out: Record<string, string> = {};
  if (!headersInit) return out;
  if (headersInit instanceof Headers) {
    headersInit.forEach((value, key) => (out[key] = String(value)));
  } else if (Array.isArray(headersInit)) {
    for (const [k, v] of headersInit) out[k] = String(v);
  } else if (typeof headersInit === 'object') {
    Object.entries(headersInit).forEach(([k, v]) => (out[k] = String(v as any)));
  }
  return out;
}

function generateIdempotencyKey(): string {
  try {
    const globalCrypto: any = (typeof crypto !== 'undefined' && crypto) || (typeof (window as any).crypto !== 'undefined' && (window as any).crypto);
    if (globalCrypto && typeof globalCrypto.randomUUID === 'function') return globalCrypto.randomUUID();
  } catch (e) {
    // ignore
  }
  return `idemp_${Math.random().toString(36).slice(2)}_${Date.now()}`;
}

function parseBody(body: any): any {
  if (body === undefined || body === null) return undefined;
  if (typeof body === 'string') {
    try {
      return JSON.parse(body);
    } catch (e) {
      return body;
    }
  }
  return body;
}

function buildFetchHeaders(h: Record<string, string>): Headers {
  const headers = new Headers();
  Object.entries(h || {}).forEach(([k, v]) => headers.set(k, v));
  return headers;
}

/**
 * Main mutator function used by generated client.
 * Accepts either (url: string, init?: RequestInit) or (configObject) shapes.
 * Returns an Orval-like envelope: { data, status, headers }
 */
export async function customInstance<T = any>(request: any, init?: any): Promise<T> {
  // Normalize inputs
  let url: string = '';
  let method: string = 'GET';
  let headersObj: Record<string, string> = {};
  let body: any = undefined;

  if (typeof request === 'string') {
    url = request;
    method = (init && (init.method as string)) || 'GET';
    headersObj = toPlainHeaders(init?.headers);
    body = init?.body;
  } else if (request && (request.url || request.path)) {
    url = request.url || request.path;
    method = (request.method || request.init?.method || 'GET') as string;
    headersObj = toPlainHeaders(request.init?.headers || request.headers);
    body = request.body ?? request.init?.body ?? request.data ?? request.payload;
  } else if (request && typeof request === 'object' && request !== null) {
    // Best-effort normalization for unknown shapes
    url = request.url || request.path || String(request);
    method = (request.method || 'GET') as string;
    headersObj = toPlainHeaders(request.headers || request.init?.headers);
    body = request.body ?? request.data ?? request.init?.body;
  } else {
    url = String(request);
    method = 'GET';
    headersObj = {};
    body = undefined;
  }

  // Prefix base URL when needed
  try {
    const base = typeof window !== 'undefined' ? window.API_BASE_URL : undefined;
    if (base && typeof url === 'string' && url.startsWith('/')) {
      url = `${base.replace(/\/$/, '')}${url}`;
    }
  } catch (e) {
    // ignore
  }

  const normalizedMethod = (method || 'GET').toUpperCase();

  // Ensure Content-Type when we have a body and not already provided
  const hasContentType = Object.keys(headersObj).some(k => k.toLowerCase() === 'content-type');
  if (!hasContentType && normalizedMethod !== 'GET' && body !== undefined) {
    headersObj['Content-Type'] = 'application/json';
  }

  // Attach Authorization if available
  try {
    const token = typeof window !== 'undefined' ? (window.__AUTH_TOKEN__ || localStorage.getItem('access_token')) : null;
    if (token && !Object.keys(headersObj).some(k => k.toLowerCase() === 'authorization')) {
      headersObj['Authorization'] = `Bearer ${token}`;
    }
  } catch (e) {
    // ignore access errors
  }

  // Ensure Idempotency-Key for mutating requests
  if (['POST', 'PUT', 'PATCH'].includes(normalizedMethod)) {
    const hasIdempotency = Object.keys(headersObj).some(k => k.toLowerCase() === 'idempotency-key');
    if (!hasIdempotency) headersObj['Idempotency-Key'] = generateIdempotencyKey();
  }

  // If offline, prefer queuing to IndexedDB Outbox (if available)
  const isOnline = typeof navigator !== 'undefined' ? navigator.onLine : true;
  if (!isOnline && typeof window !== 'undefined' && (window as any).IndexedDBOutbox) {
    // Prepare queued operation
    const queued = await (window as any).IndexedDBOutbox.addOperation({
      method: normalizedMethod,
      endpoint: url,
      data: parseBody(body),
      headers: headersObj,
      maxRetries: 3
    });

    // Return Orval-like envelope indicating operation queued
    return {
      data: { queued: true, id: queued && queued.id },
      status: 202,
      headers: new Headers()
    } as unknown as T;
  }

  // Prefer window.APIConfig.makeRequest if provided (test shims rely on this signature)
  if (typeof window !== 'undefined' && window.APIConfig && typeof window.APIConfig.makeRequest === 'function') {
    try {
      const makeRequest = window.APIConfig.makeRequest;
      // Many test harnesses call makeRequest(url, method, { data, headers })
      const result = await makeRequest(url, normalizedMethod, { data: parseBody(body), headers: headersObj });

      // If makeRequest already returns an Orval-like envelope, respect it
      if (result && typeof result === 'object' && ('status' in result || 'data' in result)) {
        return result as T;
      }

      // Otherwise wrap returned value into envelope
      return {
        data: result,
        status: 200,
        headers: new Headers()
      } as unknown as T;
    } catch (err) {
      // fallback to fetch
      // eslint-disable-next-line no-console
      console.warn('APIConfig.makeRequest failed, falling back to fetch:', err);
    }
  }

  // Optimistic PUT handling
  if (['PUT', 'PATCH'].includes(normalizedMethod) && typeof window !== 'undefined' && (window as any).OptimisticAPIClient && typeof (window as any).OptimisticAPIClient.putWithOptimisticLock === 'function') {
    try {
      const res = await (window as any).OptimisticAPIClient.putWithOptimisticLock(url, parseBody(body), { idempotencyKey: headersObj['Idempotency-Key'] });
      return {
        data: res,
        status: 200,
        headers: new Headers()
      } as unknown as T;
    } catch (err) {
      // continue to normal fetch flow
    }
  }

  // Final fetch fallback
  const finalHeaders = buildFetchHeaders(headersObj);
  const fetchOptions: RequestInit = {
    method: normalizedMethod,
    headers: finalHeaders,
    body: normalizedMethod === 'GET' || normalizedMethod === 'HEAD' ? undefined : (typeof body === 'string' ? body : JSON.stringify(parseBody(body)))
  };

  const resp = await fetch(url, fetchOptions);

  const text = await resp.text();
  const contentType = resp.headers.get('content-type') || '';
  const isJson = contentType.includes('application/json');

  if (!resp.ok) {
    // try parse JSON error
    let parsed: any = undefined;
    if (isJson) {
      try { parsed = JSON.parse(text); } catch (e) { parsed = text; }
    }
    const error: any = new Error((parsed && parsed.error) || resp.statusText || 'HTTP Error');
    error.status = resp.status;
    error.data = parsed ?? text;
    throw error;
  }

  const data = isJson ? (function() { try { return JSON.parse(text); } catch (e) { return text; } })() : text;

  return {
    data,
    status: resp.status,
    headers: resp.headers
  } as unknown as T;
}

export default customInstance;

// Expose to window for compatibility/fallback (when running as a browser script)
if (typeof window !== 'undefined') {
  (window as any).customInstance = customInstance;
}
